import React,{useState} from 'react'

export default function Form(props) {
    const handleUpClick = ()=> {
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert("Converted to UpperCase!","success");
    }
    const handleLoClick = ()=> {
        let newText = text.toLowerCase();
        setText(newText)
        props.showAlert("Converted to LowerCase!","success")
    }
    const handleCapitalize = () => {
        let newText = text;
        if (text === text.toUpperCase()) {
          newText = text.charAt(0).toUpperCase() + text.slice(1).toLowerCase();
        } else {
          newText = text.charAt(0).toUpperCase() + text.slice(1);
        }
        setText(newText);
        props.showAlert("First letter capitalized!", "success");
      };
      
    const handleAlternating = ()=> {
        let newText = text.split('').map((char, index) => {
            if (index % 2 === 0) {
                return char.toUpperCase();
            } else {
                return char;
            }
        }).join('');
        setText(newText)
        props.showAlert("Capitalized Alternating Letters!","success")
    }
    const handleReverse = ()=> {
        let newText = text.split('').map((char, index) => {
            if (char === char.toUpperCase()) {
                return char.toLowerCase();
            } else {
                return char.toUpperCase();
            }
        }).join('');
        setText(newText)
        props.showAlert("Reversed the case!","success")
    }
    const handleExtSpace = ()=> {
        let newText = text.split(/[ ]+/)
        setText(newText.join(" "))
        props.showAlert("Cleared extra spaces!","success")
    }
    const handleClear = ()=> {
        let newText = '';
        setText(newText)
        props.showAlert("Cleared text!","success")
    }
    const handleOnChange = (event)=> {
        setText(event.target.value)
       }
   
  const [text, setText] = useState('');
//   setText("New Text");
  return (
<>
<div className='container' style = {{color : props.mode==='dark' ? 'white' : '#0c0927'}}>
    <h1 >{props.heading}</h1>
    <div className="mb-3">
    <textarea className="form-control" value={text} style = {{backgroundColor : props.mode==='dark' ? '#0c0927' : 'white', color : props.mode==='dark' ? 'white' : '#0c0927'}} onChange={handleOnChange} id="box" rows="8"></textarea>
    </div>
    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleUpClick} >Convert to UpperCase</button>
    <button disabled={text.length===0} className="btn btn-secondary mx-1 my-1" onClick={handleCapitalize} >Capitalize First Letter</button>
    <button disabled={text.length===0} className="btn btn-success mx-1 my-1" onClick={handleLoClick} >Convert to LowerCase</button>
    <button disabled={text.length===0} className="btn btn-warning mx-1 my-1" onClick={handleAlternating} >Capitalize AlTeRnAtInG Letters</button>
    <button disabled={text.length===0} className="btn btn-info mx-1 my-1" onClick={handleReverse} >Reverse Case</button>
    <button disabled={text.length===0} className="btn btn-secondary my-1 mx-1 my-1" onClick={handleExtSpace} >Remove extra space</button>
    <button disabled={text.length===0} className="btn btn-danger my-1 mx-4 my-1" onClick={handleClear} >Clear text</button>
</div>


<div className="container my-3" style = {{color : props.mode==='dark' ? 'white' : '#0c0927'}}>
    
    <h2>Your text summary here:</h2>
    <p>No. of words : {text.split(/\s+/).filter((element)=>{return element.length!=0}).length} words</p>
    <p>No. of characters : {text.length} characters(with spaces) and {text.split(' ').join('').length} characters(without spaces).</p>
    <p>The text can be read in {0.008 *text.split(" ").filter((element)=>{return element.length!=0}).length} minutes.</p>
    <p>No. of vowels and consonants: {text.match(/[aeiou]/gi) ? text.match(/[aeiou]/gi).length : 0} vowels and {text.match(/[bcdfghjklmnpqrstvwxyz]/gi) ? text.match(/[bcdfghjklmnpqrstvwxyz]/gi).length : 0} consonants</p>
</div>
</>
  )
}
